import { Component, OnInit } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { EventLogService } from '../../eventlog.service';
import { Eventlog } from '../../eventlog';
import { Router, ActivatedRoute } from '@angular/router';
import { IMyDrpOptions, IMyDateRangeModel} from 'mydaterangepicker';

@Component({
  selector: 'eventloglist',
  templateUrl: './eventloglist.component.html',
  styleUrls: ['./eventloglist.component.scss'],
  providers: [EventLogService]
})
export class EventLogListComponent {
  
    constructor(private _eventlogService: EventLogService, private router: Router, private route: ActivatedRoute){
        this.getEventLogLines();
    }

    private myDateRangePickerOptions: IMyDrpOptions = {
        dateFormat: 'dd/mm/yyyy',
        height: '24px',
        width: '225px'
    };

    // dateRangeChanged callback function called when the user apply the date range. This is
    // mandatory callback in this option. There are also optional inputFieldChanged and
    // calendarViewChanged callbacks.
    onDateRangeChanged(event: IMyDateRangeModel) {
        console.log('onDateRangeChanged(): Begin date: ', event.beginDate, ' End date: ', event.endDate);
        console.log('onDateRangeChanged(): Formatted: ', event.formatted);
        console.log('onDateRangeChanged(): BeginEpoc timestamp: ', event.beginEpoc, ' - endEpoc timestamp: ', event.endEpoc);
        
        this._eventlogService.getDataByDate(event.beginDate, event.endDate)
            .then(eventLogLines => {
                this.source.load(eventLogLines)
                this.source.setSort([{ field: 'timestamp', direction: 'desc' }])
            })
            .catch(error => this.errorMessage = <any>error);
    
    }

    source: LocalDataSource = new LocalDataSource();

    public eventLogLines: Eventlog[];
    private errorMessage: any = '';

    getEventLogLines() {
        this._eventlogService.getData()
            .then(eventLogLines => {
                this.source.load(eventLogLines)
                this.source.setSort([{ field: 'timestamp', direction: 'desc' }])
            })
            .catch(error => this.errorMessage = <any>error);
    }

    addLogLines() {
        this._eventlogService.setData()
            .then(data => data)
            .catch(error => this.errorMessage = <any>error); 
    }
    
    refresh() {
        this.getEventLogLines();
    }
    
    onUserRowSelect(event): void {
        this.router.navigate(['../view', { 'taskid': event.data.taskid, 'host': event.data.host}], { relativeTo: this.route });
    }
    
    settings = {
        actions: {
            add: false,
            edit: false,
            delete: false
        },
        pager: {
            display: false,
            perPage: 15
        },
        filter : {
            filter: false
        },
        columns: {
            timestamp: {
                title: 'Timestamp',
                width: '5%',
            },
            playbook: {
                title: 'Task Group',
                width: '20%'
            },
            task: {
                title: 'Task',
                width: '20%'
            },
            // taskid: {
            //     title: 'Task Id',
            //     width: '20%'
            // },
            user: {
                title: 'User',
                width: '20%'
            },
            host: {
                title: 'Hostname',
                width: '10%',
            },
            status: {
                title: 'Status',
                width: '20%',
                filter: {
                    type: 'list',
                    config: {
                        selectText: 'All',
                        list: [
                            { value: 'OK', title: 'OK'},
                            { value: 'Failed', title: 'Failed'},
                            { value: 'Unreachable', title: 'Unreachable'}
                        ]
                    }
                }
            },
            msg: {
                title: 'Message',
                width: '20%'
            }
        }
    };
}



