import { Routes, RouterModule } from '@angular/router';

import { EventLogComponent } from './eventlog.component';
import { EventLogListComponent } from './components/list/eventloglist.component';
import { EventLogViewComponent } from './components/view/eventlogview.component';

const routes: Routes = [
  { path: '', component: EventLogComponent },
  { path: 'list', component: EventLogListComponent },
  { path: 'view', component: EventLogViewComponent },
];

export const routing = RouterModule.forChild(routes);


