import { NgModule }      from '@angular/core';
import { CommonModule }  from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgaModule } from '../../theme/nga.module';
import { Ng2SmartTableModule } from 'ng2-smart-table';
import { routing } from './eventlog.routing';

import { EventLogComponent } from './eventlog.component';
import { EventLogListComponent } from './components/list/eventloglist.component';
import { EventLogViewComponent } from './components/view/eventlogview.component';

import { MyDateRangePickerModule } from 'mydaterangepicker';
import { SafeHtmlPipe } from './components/view/eventlogview.component'; 

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    NgaModule,
    Ng2SmartTableModule,
    routing,
    MyDateRangePickerModule,
  ],
  declarations: [
    EventLogComponent,
    EventLogListComponent,
    EventLogViewComponent,
    SafeHtmlPipe
  ]
})
export class EventLogModule {}


